// SECTION scheduler :: START

.scheduler {
  &__header {
    overflow: hidden;
    position: relative;
    background-color: #ffffff;

    .c-grid {
      position: relative;
    }

    &-wrapper {
      display: flex;
      align-items: center;
      height: 158px;

      &-left {
        display: flex;
        align-items: center;

        span {
          display: block;
          width: 2px;
          height: 38px;
          margin-left: 20px;
          margin-right: 20px;
          background-color: rgba(#00449D, 0.1);
        }

        p {
          font-weight: 300;
          font-size: 20px;
          color: #000;
        }
      }
    }

    &-marker {
      position: absolute;
      top: -6px;
      right: 0;
    }

    svg {
      fill: #00449D;
    }
  }
}

// SECTION scheduler :: END